.toDoApp {
  display:flex;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  justify-content: center;
  background: #222;
  width: 80%;
  padding: 40px 20px;
  color: #fff;
  border-radius: 20px;
}
.todo-form {
  margin-top: 15px;
}
.todo-button {
  margin-left: 20px;
  border: 2px solid #fff ;
  border-radius: 20px;
  padding: 5px 10px;
  color:#fff;
  background: #222;
}
.todo-row {
  padding: 20px;
  margin-top: 14px;
  background: #444; 
  font-size: 28px;
  border-radius: 20px;
}
.icons {
  font-size: 20px;
}
